import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import MentorExportCard from "./MentorExportCard";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import {
  useMentorAllExportList,
  useMentorMenteeExportList,
} from "../../../Hooks/ApiHooks/MentorManager/mentor-manager-api";
import style from "../../FinanceManager/FinanceManager.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import MentorAttendanceExport from "./MentorAttendanceExport";
const MentorExport = ({ mid, instituteId }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [openStep, setOpenStep] = useState("");

  const [mentorMenteeExportList] = useMentorMenteeExportList();
  const { mentorAllExportList, mentorAllExportListLoading } =
    useMentorAllExportList({
      data: {
        mid: mid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !mid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (mentorAllExportList?.list?.length) {
      let obj = nextPage(mentorAllExportList?.list?.length);
      setState(() => obj);
    }
  }, [mentorAllExportList?.list?.length]);
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onClose = () => {
    setOpenStep("");
  };

  const onExport = () => {
    if (mid) {
      setDisabled((pre) => !pre);
      mentorMenteeExportList({
        mid: mid,
      })
        .then((res) => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("data_export")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "250px",
                    }}
                  />
                </div>
              </section>
              <CreateButton
                label="export_mentee"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onExport}
              />
              <CreateButton
                label="export_attendance"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={() => setOpenStep(1)}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          {mentorAllExportList?.list?.map((excel, index) =>
            mentorAllExportList?.list?.length === index + 1 ? (
              <div key={excel?._id} ref={ref}>
                <MentorExportCard excel={excel} instituteId={instituteId} />
              </div>
            ) : (
              <MentorExportCard
                excel={excel}
                key={excel?._id}
                instituteId={instituteId}
              />
            )
          )}
          {mentorAllExportListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            mentorAllExportList?.list?.length === 0 && (
              <EmptyMessage
                title="No excel found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            mentorAllExportList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No excel added."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {openStep === 1 ? (
        <MentorAttendanceExport onClose={onClose} mid={mid} />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default MentorExport;
