import { mentorManagerApi } from "./mentorManagerApi";
import {
  mentorAllExportListFunction,
  mentorMenteeExportListFunction,
  mentorMenteeAttendanceExportListFunction,
} from "./mentor_manager_query_mutation_function";

const mentorManagerApiDynamic = mentorManagerApi.injectEndpoints({
  endpoints: (builder) => ({
    mentorAllExportList: mentorAllExportListFunction(builder),
    mentorMenteeExportList: mentorMenteeExportListFunction(builder),
    mentorMenteeAttendanceExportList:
      mentorMenteeAttendanceExportListFunction(builder),
  }),
});
export const {
  useMentorAllExportListQuery,
  useMentorMenteeExportListMutation,
  useMentorMenteeAttendanceExportListMutation,
} = mentorManagerApiDynamic;
