import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import useCalendar from "../../../../MembersTab/staff/Mainbody/FinanceManager/Payroll/Employee/useCalender";
import { useMentorMenteeAttendanceExportList } from "../../../Hooks/ApiHooks/MentorManager/mentor-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import CreateButton from "../../../Ui/Button/CreateButton";
import tileStyle from "../../../../Department/Institute/Components/exportData/Tile.module.css";
const MentorAttendanceExport = ({ onClose, mid }) => {
  const { t } = useTranslation();
  const [isType, setIsType] = useState("");
  const [disabled, setDisabled] = useState(false);

  const { daysInMonth, selectedDate, monthNames, getNextMonth, getPrevMonth } =
    useCalendar();

  const [mentorMenteeAttendanceExportList] =
    useMentorMenteeAttendanceExportList();
  const onNext = () => {
    getNextMonth();
  };
  const onPrev = () => {
    getPrevMonth();
  };

  const onType = (val) => {
    setIsType(val);
  };

  const onExportData = () => {
    if (mid) {
      setDisabled((pre) => !pre);
      let newDate = new Date(
        `${daysInMonth} ${
          monthNames[selectedDate.getMonth()]
        } ${selectedDate.getFullYear()}`
      );
      newDate = newDate.toISOString();
      mentorMenteeAttendanceExportList({
        mid: mid,
        type: isType,
        month: `${newDate.substring(5, 7)}`,
        year: `${newDate.substring(0, 4)}`,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch(() => {});
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("filter_and_export_student_attendance")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <section
            className={tileStyle.tile_tile_container}
            style={{
              marginTop: "1rem",
            }}
          >
            <div
              className={
                isType === "" ? tileStyle.tile_tile_active : tileStyle.tile_tile
              }
              onClick={() => onType("")}
            >
              {t("monthly")}
            </div>
            <div
              className={
                isType === "ALL"
                  ? tileStyle.tile_tile_active
                  : tileStyle.tile_tile
              }
              onClick={() => onType("ALL")}
            >
              {t("semester")}
            </div>
          </section>
          {!isType ? (
            <section className={tileStyle.fees_head_title}>
              <img
                src="/images/members/back-icon-white.svg"
                style={{
                  cursor: "pointer",
                }}
                onClick={onPrev}
              />

              <h6 className={`${tileStyle.yearmonth}`}>
                {`${monthNames[selectedDate.getMonth()]}`}{" "}
                {`${selectedDate.getFullYear()}`}
              </h6>

              <img
                src="/images/members/forward-icon-white.svg"
                style={{
                  cursor: "pointer",
                }}
                onClick={onNext}
              />
            </section>
          ) : null}
          <CreateButton label="export" onAction={onExportData} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default MentorAttendanceExport;
