import { PATCH } from "../UrlConfig/MethodType";

export const mentorAllExportListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/mentor/mentee/export/one/mentor/${data?.mid}/excel/query`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.excel_arr,
        };
      else
        return {
          page: arg?.page,
          list: response?.excel_arr,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },
    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

export const mentorMenteeExportListFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/mentor/mentee/export/one/mentor/${data?.mid}/all/mentee/query`,
      method: PATCH,
    }),
  });
};

export const mentorMenteeAttendanceExportListFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/mentor/mentee/export/one/mentor/${data?.mid}/mentee/attendance/query?month=${data?.month}&year=${data?.year}&type=${data?.type}`,
      method: PATCH,
    }),
  });
};
