import {
  useMentorAllExportListQuery,
  useMentorMenteeAttendanceExportListMutation,
  useMentorMenteeExportListMutation,
} from "../../../Services/MentorManager/mentorManagerApiDynamic";

export const useMentorAllExportList = (args) => {
  const { data, refetch, isFetching } = useMentorAllExportListQuery(args.data, {
    skip: args.skip,
  });
  return {
    mentorAllExportList: data,
    mentorAllExportListRefetch: refetch,
    mentorAllExportListLoading: isFetching,
  };
};

export const useMentorMenteeExportList = () => {
  const [mentorMenteeExportList] = useMentorMenteeExportListMutation();
  return [mentorMenteeExportList];
};

export const useMentorMenteeAttendanceExportList = () => {
  const [mentorMenteeAttendanceExportList] =
    useMentorMenteeAttendanceExportListMutation();
  return [mentorMenteeAttendanceExportList];
};
